body {
    border-color: #E5E5E5;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: black;
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
  }
  
  * {
    box-sizing: border-box;
  }
  
  .container {
    padding: 16px;
    background-color: white;
  }

  label
  {
      margin-left: 20px;
      padding-top: 20px;
  }


  
  input[type=text],input[type=email], input[type=password]{
    width: 90%;
    padding: 15px;
    margin: 5px 0 22px 20px;
    display: inline-block;
    border: none;
    background: #f1f1f1;
  }
  
  input[type=text]:focus, input[type=password]:focus{

    background-color: #ddd;
    outline: none;
  }
  

  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }
  

  .registerbtn {
    background-color: #A4B96D;
    color: white;
    padding: 16px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 90%;
    opacity: 0.9;
    margin: 5px 0 22px 20px;
  }
  
  .registerbtn:hover {
    opacity: 1;
  }
  

  @media only screen and (max-width: 1180px) {

    .image-wrap {
      width: 300px;
      top: 5%;
      position: fixed;
  }
}